          'ko': '<p>flexbox와 별개로 CSS 그리드를 설정하면 컬럼과 행 두가지 측면에서 쉽게 그리드 항목을 배치할 수 있습니다. <code>grid-row-start</code>는 <code>grid-column-start</code> 수직선을 제외하곤 동일하게 작동합니다.</p><p><code>grid-row-start</code>를 이용하여 당근에 물을 주십시오.</p>',
